  }

  .ongoing {
    position: absolute;
    font-size: 30px;
    margin-top: -40px;
    margin: -20px auto;
    margin-left: 208px;
    -webkit-animation: color-change-green 3s infinite;
            animation: color-change-green 3s infinite;
  }

  .to-token {
    background: transparent;
    display: block;
    position: relative;
    top: 245px;
