  }
  
   /* Color animation */
  
  @-webkit-keyframes color-change-red {
    50% { color: purple; }
    50% { color: rgb(163, 25, 25); }
  }
  
  @keyframes color-change-red {
    50% { color: purple; }
    50% { color: rgb(163, 25, 25); }
  }

  @-webkit-keyframes color-change-green {
    50% { color: rgb(255, 255, 255); }
    50% { color: rgb(73, 216, 29); }
  }

  @keyframes color-change-green {
    50% { color: rgb(255, 255, 255); }
    50% { color: rgb(73, 216, 29); }
  }
  
